section.hero {
    .banner-wrap {
        position: relative;

        .banner-image {
            height: 40vh;

            @media (min-width: 992px) {
                height: 90vh;
            }

            @media(orientation: landscape) {
                height: 90vh;
            }


            img {
                width: 100%;

                @supports(object-fit: cover) {
                    height: 100%;
                    object-fit: cover;
                }

            }
        }

        .sticker {
            width: 130px;
            height: 130px;
            position: absolute;
            right: -15px;
            top: -15px;

            img {
                width: 100%;
            }

            .sticker-inner {
                width: 73%;
                height: 73%;
                border-radius: 50%;
                border: 2px dashed #c6aa8d;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                text-align: center;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

                .title {
                    border-bottom: 1px dashed #c6aa8d;
                    margin-top: 5px;

                    h3 {
                        .resetmp;
                        font-family: 'Times New Roman', Times, serif;
                        color: @font-color-1;
                        text-transform: uppercase;
                        font-weight: bold;
                        letter-spacing: 2px;
                        font-size: 11px;

                        &:last-of-type {
                            color: @font-color-2;
                            font-style: italic;
                            font-weight: bolder;
                            margin-bottom: 5px;
                        }
                    }
                }

                h4 {
                    margin-top: 5px;
                    font-family: "Adelle-SemiBold";
                    text-transform: uppercase;
                    font-style: italic;
                    color: @font-color-3;
                    font-size: 11px;
                }
            }


            @media (min-width: 992px) {
                width: 350px;
                height: 350px;
                right: 35px;
                top: 35px;

                .sticker-inner {
                    border: 4px dashed #c6aa8d;

                    .title {
                        margin-top: 10px;
                        border-bottom: 2px dashed #c6aa8d;

                        h3 {
                            font-size: 28px !important;

                            &:last-of-type {
                                margin-bottom: 15px;
                            }
                        }
                    }

                    h4 {
                        margin-top: 20px;
                        font-size: 25px !important;
                    }
                }
            }

            @media (max-width: 991.9px) and (orientation: landscape) {
                right: 20px;
                top: 20px;
            }

            @media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {
                width: 250px;
                height: 250px;
                right: 20px;
                top: 20px;

                .sticker-inner {
                    border: 4px dashed #c6aa8d;

                    .title {
                        margin-top: 10px;
                        border-bottom: 2px dashed #c6aa8d;

                        h3 {
                            font-size: 20px;

                            &:last-of-type {
                                margin-bottom: 15px;
                            }
                        }
                    }

                    h4 {
                        margin-top: 20px;
                        font-size: 20px;
                    }
                }
            }
        }
    }
}