header.main-header {
    position: relative;
    overflow: hidden;
    width: 100%;

    .design-wrap {
        width: 100%;
        position: relative;
        height: 25vh;

        @media (min-width: 992px) {
            height: 38vh;
        }

        @media (max-width:992px) and (orientation: landscape) {
            height: 40vh;
        }


        .elipse {
            .resetmp;
            width: 50%;
            height: 250px;
            background-color: @background-color;
            border-radius: 50%;

            @supports(clip-path: ellipse(50% 20% at 50% 20%)) {
                border-radius: 0;
                -webkit-clip-path: ellipse(50% 20% at 50% 20%);
                clip-path: ellipse(50% 20% at 50% 20%);
            }

            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
        }

        .small-rect {
            background-color: @background-color;
            width: 80%;
            height: 50px;
            position: absolute;
            top: 35px;
            left: 50%;
            transform: translateX(-50%);
            border-top-right-radius: 30px;
            border-top-left-radius: 30px;
        }

        .medium-rect {
            background-color: @background-color;
            width: 90%;
            height: 50px;
            position: absolute;
            top: 50px;
            left: 50%;
            transform: translateX(-50%);
            border-top-right-radius: 30px;
            border-top-left-radius: 30px;
        }

        .large-rect {
            background-color: @background-color;
            width: 100%;
            height: 100% !important;
            position: absolute;
            top: 90px;
            left: 50%;
            transform: translateX(-50%);
            border-top-right-radius: 30px;
            border-top-left-radius: 30px;
        }
    }

    .header-title {
        position: absolute;
        top: 52%;
        left: 50%;
        transform: translate(-50%, -50%);

        h2 {
            font-family: "Anton-Reg";
            color: @font-color-1;
            text-transform: capitalize;
            letter-spacing: 2px;
            font-size: 24px;

            span {
                color: @font-color-2;
            }

            @media (min-width: 992px) {
                font-size: 34px;
            }
        }

        &::before {
            content: url("../images/hearts.png");
            position: absolute;
            top: -55px;
            left: 50%;
            transform: translateX(-50%) scale(.7);
            opacity: .6;
        }

        &::after {
            content: url("../images/hearts.png");
            position: absolute;
            bottom: -45px;
            left: 50%;
            transform: translateX(-50%) scale(.7) rotate(180deg);
            opacity: .6;
        }
    }
}