section.gallery {
    header {
        text-align: center;
        padding: 50px 0;

        img {
            width: 100px;
            margin-bottom: 10px;
        }

        h4 {
            font-family: "Lato-SemiBold";
            color: @font-color-2;
            text-transform: uppercase;
            letter-spacing: 1px;
            // font-size: 24px;

            span {
                font-family: "Lato-Heavy";
                color: @font-color-1;
            }
        }
    }

    #photos {
        /* Prevent vertical gaps */
        line-height: 0;
        -webkit-column-count: 1;
        -webkit-column-gap: 0px;
        -moz-column-count: 1;
        -moz-column-gap: 0px;
        column-count: 1;
        column-gap: 20px;
        margin: 0 auto;

        .image-wrap {
            display: block;
            border-radius: 20px !important;
            overflow: hidden;
            margin-bottom: 20px;
            box-shadow: 2px 2px 2px 0px rgba(128, 128, 128, 0.3);

            img {
                /* Just in case there are inline attributes */
                width: 100% !important;
                height: auto !important;
            }
        }

        // Small devices (landscape phones, 576px and up)
        @media (min-width: 576px) {
            -webkit-column-count: 2;
            -moz-column-count: 2;
            column-count: 2;
        }

        // Medium devices (tablets, 768px and up)
        @media (min-width: 768px) {
            -webkit-column-count: 3;
            -moz-column-count: 3;
            column-count: 3;
        }

        // Large devices (desktops, 992px and up)
        @media (min-width: 992px) {
            -webkit-column-count: 3;
            -moz-column-count: 3;
            column-count: 3;
        }

    }

    .loadmore-button {
        text-align: center;
        padding: 50px 0;

        .loadMore {
            font-family: "Adelle-SemiBold";
            text-transform: uppercase;
            border-radius: 50px;
            background-color: @font-color-5;
            color: white;
            border: none;
            padding: 14px 25px 10px 25px;
            font-size: 11px;
            letter-spacing: 1px;
        }
    }
}