footer {
    .wave-border {
        padding: 60px 0;
        width: 100%;
        background-color: darken(@primary-color, 15%);
        position: relative;

        &::after {
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            top: -25px;
            height: 60px;
            background: radial-gradient(closest-side, darken(@primary-color, 15%), darken(@primary-color, 15%) 50%, darken(@primary-color, 15%) 100%, transparent 50%);
            background-repeat: repeat-x;
            background-size: 38px 40px;
            background-position: -10px 10px;
            z-index: 222;
        }

        &::before {
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            top: -31px;
            height: 60px;
            background: radial-gradient(closest-side, darken(@primary-color, 25%), darken(@primary-color, 25%) 50%, darken(@primary-color, 25%) 100%, transparent 50%);
            background-repeat: repeat-x;
            background-size: 38px 40px;
            background-position: -10px 10px;
            z-index: 111;
        }

        h2 {
            font-family: 'Times New Roman', Times, serif;
            color: @font-color-1;
            text-transform: uppercase;
            font-weight: bold;
            text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4);
            letter-spacing: .5px;
            text-align: center;
            margin-bottom: 30px;
            font-size: 26px;

            span {
                font-style: italic;
                color: @font-color-4;
            }
        }


        // Small devices (landscape phones, less than 768px)
        @media (min-width: 767.98px) {
            h2 {
                font-size: 26px
            }
        }

        // Medium devices (tablets, less than 992px)
        @media (min-width: 991.98px) {
            h2 {
                font-size: 28px
            }
        }

        // Large devices (desktops, less than 1200px)
        @media (min-width: 1199.98px) {
            h2 {
                font-size: 34px;
            }
        }
    }

    .social-icons {
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        .icon-wrap {
            width: 45px;
            height: 45px;
            border-radius: 50%;
            overflow: hidden;
            display: flex;
            justify-content: center;
            align-items: center;
            box-shadow: -1px -1px 3px 0px rgba(0, 0, 0, 0.4);

            img {
                width: 20px;
            }
        }

        .twi {
            background-color: #57e9ff;
            border-bottom: 2px solid darken(#57e9ff, 5%);
            border-right: 2px solid darken(#57e9ff, 5%);

        }

        .fb {
            background-color: #576bff;
            border-bottom: 2px solid darken(#576bff, 5%);
            border-right: 2px solid darken(#576bff, 5%);
        }

        .pin {
            background-color: #ff5768;
            border-bottom: 2px solid darken(#ff5768, 5%);
            border-right: 2px solid darken(#ff5768, 5%);
        }
    }

    .foo-bar {
        background-color: darken(@primary-color, 45%);
        opacity: .5;
        height: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;

        p {
            .resetmp;
            font-family: "Montserrat-Bold";
            color: white;
            text-transform: capitalize;
            padding: 0 20px;

            span {
                font-family: "Montserrat-Reg";
                text-transform: uppercase;
            }
        }
    }
}