div.banner {
    text-align: center;
    // background: linear-gradient(rgba(0, 0, 0, .5), rgba(0, 0, 0, 0.5)),
    //     url("../images/mid-banner.png");
    background-image: url("../images/mid-banner.png");
    background-position: center;
    background-size: cover;
    padding: 50px 0;
    position: relative;

    &::before {
        content: "";
        width: 100%;
        height: 100%;

        background-color: #252525;
        opacity: .7;
        position: absolute;
        top: 0;
        left: 0;
    }

    h1 {
        .resetmp;
        font-family: "Appetite";
        color: @font-color-1;
        text-shadow: 1px 1px 2px black;
        font-size: 18px;

        &:first-of-type {
            color: darken(@font-color-1, 10%);
        }

        &:nth-of-type(2n) {
            color: @font-color-4;
        }

        &:last-of-type {
            color: @font-color-5;
        }
    }

    @media (min-width: 576px) {
        h1 {
            font-size: 24px;
        }
    }

    @media (min-width: 768px) {
        padding: 70px 0;

        h1 {
            font-size: 30px;
        }
    }

    @media (min-width: 992px) {
        h1 {
            font-size: 40px;
        }
    }
}